import React, { Component } from 'react';
import axios from 'axios';
// Sweet Alert library obtained from https://github.com/sweetalert2/sweetalert2
import Swal from 'sweetalert2'

export default class Weekly extends Component {
    constructor() {
        super();
        this.state = {
            weeklyHigh: '',
            weeklyLow: '',
            weeklyClose: '',
            weeklyVolume: '',
            isLoading: true
        }
    }

    // Make axios call to timer series weekly and store results in state
    getWeeklyEquityData() {
        axios({
            method:'GET',
            url: `https://www.alphavantage.co/query`,
            dataResponse: 'json',
                params: {
                    apikey: this.props.apiKey,
                    function: 'TIME_SERIES_WEEKLY',
                    symbol: this.props.stockEquitySymbol
                }
        })
        .then((result) => {
            const values = Object.values(result.data['Weekly Time Series']);
            const highValue = values[0]["2. high"];
            const lowValue = values[0]["3. low"];
            const closeValue = values[0]["4. close"];
            const volumeValue = values[0]["5. volume"];
    
            this.setState({
                weeklyHigh: Number(highValue).toFixed(2),
                weeklyLow: Number(lowValue).toFixed(2),
                weeklyClose: Number(closeValue).toFixed(2),
                weeklyVolume: volumeValue,
                isLoading: false
            });
        })
        .catch(() => {
            Swal.fire(
                'Error', 
                'You have made too many requests, please wait a minute!', 
                'error'
            );
        });
    }

    componentDidMount() {
        this.getWeeklyEquityData();
    }

    render() {
        const {
            weeklyHigh,
            weeklyLow,
            weeklyClose,
            weeklyVolume,
            isLoading
        } = this.state;

        // When the data is loading show preloader
        if (isLoading) {
            return(
                <div className="timeSeriesContainer">
                    <h3>weekly data</h3>
                    <ul>
                        <li>
                            <p>high</p>
                            <div className="preloader">
                                <div></div>
                                <div></div>
                                <div></div>
                                <div></div>
                            </div>
                        </li>
                        <li>
                            <p>low</p>
                            <div className="preloader">
                                <div></div>
                                <div></div>
                                <div></div>
                                <div></div>
                            </div>
                        </li>
                        <li>
                            <p>close</p>
                            <div className="preloader">
                                <div></div>
                                <div></div>
                                <div></div>
                                <div></div>
                            </div>
                        </li>
                        <li>
                            <p>volume</p>
                            <div className="preloader">
                                <div></div>
                                <div></div>
                                <div></div>
                                <div></div>
                            </div>
                        </li>
                    </ul>
                </div>
            )
        } 

        return (
            <div className="timeSeriesContainer">
                <h3>weekly data</h3>
                <ul>
                    <li>
                        <p>high</p>
                        <p>{weeklyHigh}</p>
                    </li>
                    <li>
                        <p>low</p>
                        <p>{weeklyLow}</p>
                    </li>
                    <li>
                        <p>close</p>
                        <p>{weeklyClose}</p>
                    </li>
                    <li>
                        <p>volume</p>
                        <p>{weeklyVolume}</p>
                    </li>
                </ul>
            </div>
        );
    };
};
